/**
 * PayCheck footer
 * Contains the final price breakdown with tax + tips*/

import React, { useState, useEffect } from 'react';
import CustomerFooter from './layout/CustomerFooter';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { formatCurrency } from './Helper';
import {AiOutlineLoading3Quarters} from 'react-icons/ai';
export default function PayCheckFooter(props) {
  const renderTipPrice = () => {
    let tip = 0;
    if (!props.tipAmount) {
      return null;
    }
    return (
      <div className='d-flex mb-1 justify-content-between align-items-center'>
        <div>Tip</div>
        <div className='fw-bold'>{formatCurrency(props.tipAmount)}</div>
      </div>
    );
  };
  /**
   * The tip section has the ability to calculate via percentage of total price. We need to calculate it here*/
  const calcTotalPrice = () => {
    // normal $ value but tip is 'Other'  
    let total = props?.total;
    // tax? 
    if(total){
      total += (total * 0.01);
      return total + parseFloat(props.tipAmount || 0);
    }
    return 0;
  } 
  const handleClick = () => {
    props.onSubmit();
  };

  return (
    <CustomerFooter>
      <Container fluid>
        <div className='d-flex mb-1 justify-content-between align-items-center'>
          <div>Subtotal</div>
          <div className='fw-bold'>
            {formatCurrency(props.total)}
          </div>
        </div>
        <div className='d-flex mb-1 justify-content-between align-items-center'>
          <div>Tax</div>
          <div className='fw-bold'>
            {formatCurrency((props.total * 0.01) || 0)}
          </div>
        </div>
        {renderTipPrice()}
        <hr />
        <div className='h4 d-flex mb-3 justify-content-between align-items-center'>
          <div>Total</div>
          <div>
            {formatCurrency(calcTotalPrice())}
          </div>
        </div>
        <div className='d-grid gap-2'>
          <Button variant='primary' size='lg' onClick={handleClick}>
            { 
              props.loading ? 
              <AiOutlineLoading3Quarters className="icon-spin"/> : 
              'Pay' 
            } 
            
          </Button>
        </div>
      </Container>
    </CustomerFooter>
  );
}
